import router from 'vue-router'
import Vue from 'vue'
import Vui from '../src/vui'
Vue.use(router)

export const routerList = {
    intro: {
        title: '',
        children: [
            { meta: { title: '' }, path: 'introduce', component: () => import("@/views/intro/introduce") },
            { meta: { title: '' }, path: 'install', component: () => import("@/views/intro/install") },
            { meta: { title: '' }, path: 'grateful', component: () => import("@/views/intro/grateful") },
        ]
    },
    layout: {
        title: '',
        children: [
            { meta: { title: '' }, path: 'layout', component: () => import("@/views/components/layout") },
            { meta: { title: '' }, path: 'grid', component: () => import("@/views/components/grid") },
            { meta: { title: '' }, path: 'paragraph', component: () => import("@/views/components/paragraph") },
        ]
    },
    
    basic: {
        title: '',
        children: [
            { meta: { title: '' }, path: 'icon', component: () => import("@/views/components/icon") },
            { meta: { title: '' }, path: 'button', component: () => import("@/views/components/button") },
            { meta: { title: '' }, path: 'progress', component: () => import("@/views/components/progress") },
            { meta: { title: '' }, path: 'tag', component: () => import("@/views/components/button") },
            { meta: { title: '' }, path: 'select', component: () => import("@/views/components/button") },
            { meta: { title: '' }, path: 'radio', component: () => import("@/views/components/button") },
            { meta: { title: '' }, path: 'bread', component: () => import("@/views/components/button") },
            { meta: { title: '' }, path: 'picture', component: () => import("@/views/components/button") },
            { meta: { title: '' }, path: 'title', component: () => import("@/views/components/button") },
            { meta: { title: '' }, path: 'hr', component: () => import("@/views/components/hr") },
        ]
    },

    form: {
        title: '',
        children: [
            { meta: { title: '' }, path: 'input', component: () => import("@/views/components/button") },
        ]
    },
    common: {
        title: '',
        children: [
            { meta: { title: '' }, path: 'transition', component: () => import("@/views/components/transition") },
            { meta: { title: '' }, path: 'mask', component: () => import("@/views/components/mask") },
            { meta: { title: '' }, path: 'toast', component: () => import("@/views/components/toast") },
            { meta: { title: '' }, path: 'alert', component: () => import("@/views/components/alert") },
            { meta: { title: '' }, path: 'loading', component: () => import("@/views/components/loading") },
            { meta: { title: '' }, path: 'confirm', component: () => import("@/views/components/confirm") },
            { meta: { title: '' }, path: 'modal', component: () => import("@/views/components/modal") },
            { meta: { title: '' }, path: 'scrollspy', component: () => import("@/views/components/scrollspy") },
            // { meta: { title: '' }, path: 'pin', component: () => import("@/views/components/pin") },
            { meta: { title: '' }, path: 'top', component: () => import("@/views/components/top") },
        ]
    },
}

 const docRouter = new router({
    mode: "hash",
    routes: [
        // 
        { path: '/', component: () => import('@/views/index') },

        // 
        {
            path: '/intro',
            component: () => import("@/views/layout"),
            redirect: 'intro/introduce',
            children: routerList.intro.children
        },

        // 
        {
            path: '/basic',
            component: () => import("@/views/layout"),
            redirect: 'basic/icon',
            children: routerList.basic.children
        },

        // 
        {
            path: '/layout',
            component: () => import("@/views/layout"),
            redirect: 'layout/button',
            children: routerList.layout.children
        },

        // 
        {
            path: '/common',
            component: () => import("@/views/layout"),
            redirect: 'common/mask',
            children: routerList.common.children
        },

        { path: '*', component: () => import('@/views/404') },
    ]
})

docRouter.beforeEach((to, from, next) => {
    Vui.$bar.start()
    next();
});

docRouter.afterEach(() => {
    Vui.$bar.end();
});

export default docRouter